.projects {
  height: 100vh;
  font-size: 1.5rem;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-image: url(../assets/hobby.webp);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  font-weight: 640;
  position: relative;
  overflow: hidden;
  z-index: 5;
}

.project-flex {
  margin-top: 12%;
  position: inherit;
  color: wheat;
  height: 50vh;
  z-index: 9;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.box-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 100px;
  width: 100%;
  height: 85%;
}

.box-bg {
  background-color: rgb(0, 0, 0);
  height: 346px;
  width: 306px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  overflow: hidden;
  position: relative;
  z-index: 5;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}
.box-bg .box-rotate {
  width: 150px;
  height: 550px;
  left: 25%;
  top: -40%;
  z-index: 6;
  position: absolute;
  background: linear-gradient(#00ff73, #00abfb);
  -webkit-animation: seemore-anim 10s linear infinite;
          animation: seemore-anim 10s linear infinite;
}

.box-element {
  background-color: #000000;
  border-radius: 15px;
  width: 300px;
  height: 340px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  justify-content: center;
  z-index: 7;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.box-element:hover {
  background-color: #003811;
  transform: scale(1.1);
}

.project-items {
  width: 240px;
  height: 240px;
  background-repeat: no-repeat;
  background-size: 200px;
  background-position: center;
}

.project-text {
  text-align: center;
  padding-top: 20px;
  border-top: 3px solid wheat;
  border-left: 3px solid transparent;
  border-right: 3px solid transparent;
}

.clicker-game {
  background-image: url(../assets/icons/pickaxe.png);
}

.calculator {
  background-image: url(../assets/icons/calc.png);
}

.arm-wresling {
  background-image: url(../assets/icons/arm.png);
}

.to-do-list {
  background-image: url(../assets/icons/todo.png);
}

.seemore-bg {
  width: 75%;
  height: 100px;
  overflow: hidden;
  border-radius: 15px;
}

.seemore {
  height: 100%;
  font-size: 2rem;
  border-radius: 15px;
  cursor: pointer;
  background-color: rgb(0, 0, 0);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-in-out;
}
.seemore:hover {
  background-color: #003811;
  transform: scale(1.2);
}

@-webkit-keyframes seemore-anim {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes seemore-anim {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@media only screen and (max-width: 900px) {
  .projects {
    height: 100lvh;
    background-image: url(../assets/phoneforest.jpg);
    background-position: top center;
  }
  .project-flex {
    margin-top: -40px;
    height: 80vh;
    align-items: center;
    justify-content: center;
    gap: 6px;
  }
  .box-container {
    gap: 6px;
    align-items: center;
    justify-content: center;
  }
  .box-bg {
    width: 48vw;
    height: 206px;
  }
  .box-bg .box-rotate {
    left: 40%;
    top: -30%;
    width: 100px;
    height: 400px;
  }
  .box-element {
    width: 47vw;
    height: 200px;
  }
  .project-items {
    width: 150px;
    height: 150px;
    background-size: 100px;
  }
  .project-text {
    font-size: 1rem;
    padding-top: 10px;
  }
  .seemore-bg {
    height: 75px;
  }
}
.wave {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 15vh;
  z-index: 8;
}
