.footer {
  width: 100%;
  height: 5vh;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  justify-content: right;
  z-index: 10;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  gap: 0.7vw;
}
.footer .footer-items {
  background-repeat: no-repeat;
  background-size: contain;
  height: 2vw;
  width: 2vw;
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}
.footer .footer-items:hover {
  transform: translateY(-15px) rotate(-10deg) scale(1.2);
}
.footer .github {
  background-image: url(../assets/icons/github.png);
}
.footer .instagram {
  background-image: url(../assets/icons/instagram.png);
}
.footer .mail {
  background-image: url(../assets/icons/mail.png);
  margin-right: 1vw;
}
.footer .facebook {
  background-image: url(../assets/icons/facebook.png);
}

@media only screen and (max-width: 900px) {
  .footer {
    justify-content: center;
    gap: 30px;
    height: 40px;
  }
  .footer .footer-items {
    height: 100%;
    width: 30px;
  }
}